@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,400;0,700;0,900;1,400&display=swap);
.pd-link{
    text-decoration: none;
    color: inherit;
    transition: .3s ease-in-out;
}

.pd-link:focus, .pd-link:hover, .pd-link:visited, .pd-link:link, .pd-link:active {
    text-decoration: none;
    color: inherit;
}

.pd-link:hover{
    transform: translateY(-3px);
}

@media screen and (max-width: 767px){
    .product-img{
      margin-bottom: 1rem;
    }
} 

.counter{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 10px 0;
  }

  .counter__value{
    height: 43px;
    width: 60px;
    padding: 0 0 0 8px;
  }

  .counter__inc, .counter__dec{
    display: inline-block;
    background: #ff9f00;
    color: #fff;
    font-weight: bold;
    padding: 10px 18px;
    cursor: pointer;
  }

  .counter__dec{
    margin: 0 10px;
  }

@media screen and (max-width: 767px){
  .counter{
    margin-bottom: 1.5rem;
  }
}

.btn-container {
    display: flex;
}

.btn-container span:first-child {
    margin-right: 1rem;
}

.btn--primary {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: #fb641c;
    padding: 18px 50px;
    color: #fff;
    text-transform: uppercase;
    cursor: pointer;
}

.btn--secondary {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, .09);
    width: 4rem;
    height: 4rem;
    cursor: pointer;
}

@media screen and (max-width: 767px){
    .btn-container{
      display: flex;
      justify-content: center;
      text-align: center;
    }
}
.wl-link{
    text-decoration: none;
    color: inherit;
    transition: .3s ease-in-out;
}

.wl-link:focus, .wl-link:hover, .wl-link:visited, .wl-link:link, .wl-link:active {
    text-decoration: none;
    color: inherit;
}

.wl-link:hover{
    transform: translateY(-3px);
}

.cart-list .list-group-item + .list-group-item{
  border: 1px solid rgba(0, 0, 0, 0.125) !important;
}

.cart-remove{
    color: #fb641c;
    cursor: pointer;
}

@media screen and (max-width: 767px){
    .cart-img{
      margin-bottom: 1rem;
    }
  }

@media screen and (max-width: 767px){
    .checkout-img{
      width: 100%;
    }
  } 
#header-img {
    display: block;
    max-width: 100px;
    margin: auto;
    padding-top: 20px; 
  }
.promo-container {
    position: relative;
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .promo-title {
    font-size: 2.5rem;
    position: relative;
    width: 100%;
    line-height: 1em;
    text-transform: uppercase;
  }
  
  span {
    display: block;
  }
*::selection{
  background: none;
}

body{
  font-family: 'Source Sans Pro';
}

a:hover{
  text-decoration: none;
  color: #fff;
}
